.board {
    @include flexible(space-between, flex-start, row, nowrap);

    width: 100%;
    height: 80vh;
    padding: 1em;
    border-radius: 10px;
    background-color: #F1EEEE;
    overflow: auto;

    &__header {
        @include flexible(space-between, flex-start, row, nowrap);
        margin-bottom: 2em;
    }

    &__to-do-title, 
    &__to-do-counter {
        @include label(1rem, $colorGrayLight, .7em);
    }

    &__card {
        max-width: 300px;
        padding: 1rem;
        margin-bottom: 1rem;
        background-color: #FFFFFF;
        border-radius: 10px;
        box-shadow: 1px 1px 2px $colorGrayLight, -1px -1px 2px $colorGrayLight;
    }

    &__card-title {
        font-family: 'SF Pro Displkay Medium';
        color: $colorGrayDark;
    }

    &__card-copy {
        width: 100%;
        margin: 10px 0;
        color: $colorGray;
        line-height: 1.3em;
        overflow: hidden;
        //css below replaces text overflow with 3 dots
        //once the text is bigger than 2 lines
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }

    &__card-date {
        @include label(0.8rem, #f389bb, 5px)
    }

    &__add-new-btn {
        width: 100%;
        height: 50px;
        border: 1px solid $colorGray;
        border-radius: 10px;
        font-family: 'SF Pro Displkay Medium';

        &:hover, 
        &:focus {
            background-color: #d8d5d5;
            outline: none;
            cursor: pointer;
        }
    }
}