.header {
    @include flexible(flex-start, flex-start, column, nowrap);

    &__title {
        margin: 0 5px 0 0;
        font-family: 'SF Compact Text Bold';
        font-size: 36px;
        letter-spacing: 0px;
        color: $colorGrayDark;
    }

    &__subtitle {
        margin: 0 0 2em 0;
        font-size: 1rem;
        color:$colorGray;
    }
}